

















































import {Component, Mixins, Prop, Vue} from "vue-property-decorator";
import {formPageMixin} from "@/mixins/formPageMixin";
import {addObj, putObj} from "@/api/manage/sys/permissionApi";

@Component({
  components: {}
})
export default class PermissionForm extends Mixins(formPageMixin) {
  @Prop() title!: string;
  @Prop() parentData!: any;

  reqFunctions = {
    create: addObj,
    update: putObj
  }

  createdFormCallback() {
    if(this.parentData.id==undefined){
      this.formObj.parentId = 1;
      this.formObj.parentName = "平台";
      this.formObj.type=0;
    }
    else {
      this.formObj.parentId = this.parentData.id;
      this.formObj.parentName = this.parentData.title;
    }
  }

  echoDataProcess(data: any) {
    data.parentId = this.parentData.id;
    data.parentName = this.parentData.title;
  }

  submitSuccess(res: any) {
    this.backToPage(true);
    this.handleClose();
    this.createdFormCallback();
  }

  resetForm() {
    this.parentData = {};
    this.formObj={};
    this.buildCreatedForm();
    this.visible = false;
    this.formAction = "none";
  }
}
